
/* outer-body css */

html{
    scroll-behavior: smooth;;
    background-color:  #EBEEEE;
}

.scrollable-element {
    scrollbar-width: thin;
  }

#root{
    background-color:  #EBEEEE;
  }

/* body css */

body {
    margin: 0;
    padding:0;
    min-width:950px;
  }

.nav{
    display: none;
    width:95%;
    margin-inline: 2.5%;
    background:#003E74;
    position: fixed;
    top: 0;  
    z-index:11;
    align-content:"center";
}

#navshadow{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.navText:hover{
  background-color: #EBEEEE;
  color:#003E74;
}

#content{
  margin-inline: 3%;
  width:94%;

}
.section{
    flex-wrap: wrap;
    flex-direction: row;
    width:100%;
    height:auto;
    margin-inline: auto;
    background-color: none;
    margin-top: 50px;
    border-inline:solid 0.5px rgb(216, 216, 216);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 15px 0 rgba(0, 0, 0, 0.19);
}
.innerSection{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  background:white;
  border-radius:2px;
}

.footer{
    flex-wrap: wrap;
    flex-direction: row;
    align-content:center;
    width:96%;
    margin-inline: 2%;
    background-color: #002147;
    border-inline:solid 0.5px rgb(216, 216, 216);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25), 0 6px 20px 0 rgba(0, 0, 0, 0.2)
}

.sectionHeading{
    margin-left: 3%;
    margin-top: 2.5%;
    width:90%;
    margin-bottom:1%;
    padding-block: 1%;
    padding-inline: 1%;
    background: white;
    border-block:solid 0.5px #9D9D9D;
}

/* flex css */

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }
  
.column {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    height:100%;
    flex-wrap: wrap;
    flex: 1;
}


/* map */
pre{
    font-size: 7em;
  }
  text.pretext{
    background-color: aquamarine;
    color: black;
  }
  .shapes .country {
    cursor:pointer
  }

  .shapes .USState {
    cursor:pointer
  }

  .country:hover{
    opacity: 0.5;
  }
  
  .USState:hover{
    opacity: 0.5;
  }
  

  .shapes .graticules {
    fill: none;
    stroke: none;
  }
  
  svg.map-container{
  
    width: 100%;
    height:100%;
    display: inline-block;
    border:1px solid black;
  }



  /* clickable chart */
  #linechart{
    background: white;
  }
  pre {
    font-size: 7em;
  }
  .y-axis  path{
    z-index:10;
    stroke-width: 1px;
  }
  .tick line {
    stroke: #C0C0BB;
    stroke-width: 2px;
    z-index:-1;
    opacity: 0.5;
  }
  .tick text {
    fill: #635F5D;
    font-size: 1.2em;
  }
  .mark {
    fill: #137B80;
  }
  .x-axis-title text {
    fill: #635F5D;
  }
  .legend{
    cursor: pointer;
  }

  .tags {
    letter-spacing: 0.01em;
 
  }

  #whitetag{
    font-weight:600; 
      text-shadow:
    -0.8px -0.8px 0 #000,
     0   -0.8px 0 #000,
     0.8px -0.8px 0 #000,
     0.8px  0   0 #000,
     0.8px  0.8px 0 #000,
     0    0.8px 0 #000,
    -0.8px  0.8px 0 #000,
    -0.8px  0   0 #000;
  }
  #blacktag{
    font-weight: 800;
}
  .bar:hover{
    filter: brightness(60%);
  }
  .bar1:hover{
    filter: brightness(60%);
  }
  .bar2:hover{
    filter: brightness(60%);
  }
  circle:hover{
    filter: brightness(60%);
  }
  /* UI */

  .buttonSpace{
    padding: 3%;
    border-radius: 5px;
}



